/**

  @site-sign-up
--------------------------------------------------------
  modules/_site-sign-up.scss
--------------------------------------------------------

  Site Sign-Up

------------------------------------------------------ */
.site-sign-up {
  &__text {
    background-color: $color__black;
    border-color: $input-border-color--invert;
    color: $color__white;
    &::-webkit-input-placeholder {
      color: $color__white;
    }
  }
  &__button {
    position: absolute;
    top: 0;
    right: 0;
    padding: rem(10 15);
    color: $color__white;
  }
}



